  transition: all 300ms ease;
  cursor: pointer;
  opacity: 1;
  width: 0.375rem;
  height: 0.375rem;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.8);
}

.swiper-pagination-bullet-active {
  box-shadow: none;
  background-color: rgba(255, 255, 255, 0.8);
}

.btn-prev, .btn-next {
  @apply sq-10 z-9 static rounded-full border  flex-center text-white transition-all duration-300 cursor-pointer;
  pointer-events: auto;
  @apply hover:bg-primary-1 hover:text-white hover:border-primary-1;
}
@screen xl {
  .btn-prev, .btn-next {
    @apply absolute top-1/2 -translate-y-1/2;
  }
}
.btn-prev.swiper-button-lock, .btn-next.swiper-button-lock {
  @apply hidden;
}
.btn-prev i, .btn-next i {
  @apply title-20 font-normal;
}

.swiper-pagination-bullet-active::before {
  content: "";
  width: 1rem;
  height: 1rem;
  border: 2px solid rgba(255, 255, 255, 0.8);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: transparent;
}

