@layer utilities
	@import 'src/components/common/Title.sass'
	.section-py
		@apply py-10 lg:py-15 xl:py-20
	.no-gutters
		@apply mx-0
		> *
			@apply px-0
	.lozad
		opacity: 0
		filter: blur(10px)
		transition: .3s all ease-in-out
		&[data-loaded="true"]
			opacity: 1
			filter: blur(0)
	.scrollbar-hidden
		&::-webkit-scrollbar
			@apply hidden
		-ms-overflow-style: none // IE and Edge
		scrollbar-width: none // Firefox